import './index.less';

import 'core-js';
import 'regenerator-runtime/runtime';
import 'whatwg-fetch';
import assign from 'lodash/assign';
import './libs/bmap/index';
import featureSupports from './utils/featureDetect';
import './libs/md5/';

if (!featureSupports.cssvhunit) {
  const containerNode = document.getElementById('container');
  //
  let setTimeGetHeight;
  var containerHeight = function () {
    if (!document.body.clientHeight || document.body.clientHeight < 100 || !containerNode.offsetTop) {
      setTimeGetHeight = setTimeout(function () {
        clearTimeout(setTimeGetHeight);
        setTimeGetHeight = null;
        containerHeight();
      }, 300);
      return;
    }
    containerNode.style.height = document.body.clientHeight - containerNode.offsetTop + 'px';
  };

  containerHeight();

  window.onresize = function () {
    containerHeight();
  }
}

const FetchOptions = {
  method: 'post',
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded',
  },
  credentials: 'include',
};

const getUserInfo = jobcard => fetch(
  '/ehuodiBedrockApi/ehdrbacuserscs/selectEhdRbacUsers',
  {
    ...FetchOptions,
    body: `jobcard=${jobcard}&resultstyle=2`,
  });

const getVersionManageList = data => {
  const params = Object.keys(data)
    .map(key => `${key}=${data[key]}`)
    .join('&');
  return fetch(
    '/ehuodiBedrockApi/versionmanagecs/queryVersionManageList',
    {
      ...FetchOptions,
      body: params,
    }
  );
};

const startApp = async () => {
  //
  const userInfo = {};
  let jobcard;
  jobcard = document.cookie.match(/jobcard\=\d+/);
  if (jobcard && jobcard[0] && jobcard[0].indexOf('=') > 0) {
    jobcard = jobcard[0].split('=')[1];
  }

  try {
    const res = await getUserInfo(jobcard);
    const data = await res.json();

    if (data.result === 'error' && data.msg === 'authorityFailure') {
      if (process.env.NODE_ENV === 'development') {
        window.location.href = '/login.html';
      } else {
        window.location.href = '/scms/login.html';
      }
      return;
    }

    const currentUserData = data.data || {};
    assign(userInfo, currentUserData, {
      realname: currentUserData.username,
      mobilenumber: currentUserData.usermobilenumber,
      ehdrbacusersid: currentUserData.ehdrbacusersid,
      logindate: currentUserData.logindate,
    });
    if (Array.isArray(userInfo.userorgpros) && userInfo.userorgpros.length > 0) {
      assign(userInfo, {
        organizationcode: userInfo.userorgpros[0].organizationcode,
        organizationname: userInfo.userorgpros[0].organizationname,
        professionname: userInfo.userorgpros[0].professionname,
        professioncode: userInfo.userorgpros[0].professioncode,
        organizationtype: userInfo.userorgpros[0].organizationtype
      });
    }

    const now = moment();
    const lastlogindateTime = moment(currentUserData.lastlogindate).valueOf();
    const threeDaysAgo = moment().subtract(3, 'days');
    const dateFormat = 'YYYY-MM-DD HH:mm:ss';
    var param = {
      orderbyitem: 'updateDate DESC',
      //datestart=>-3d? :-3d
      datestart: moment(currentUserData.lastlogindate).valueOf() > threeDaysAgo.valueOf()
          ? currentUserData.lastlogindate
          : threeDaysAgo.format(dateFormat),
      dateend: now.format(dateFormat),
      status: '1',
      skipCount: 0,
      pageSize: 4,
    };
    let manageList = await getVersionManageList(param);
    manageList = await manageList.json();
    manageList = manageList.data;

    if (Array.isArray(manageList) && manageList.length > 0) {
      userInfo.fetchParam = param;
      userInfo.versionUpgrade = manageList;
      userInfo.UpgradeProjectCount = manageList.count;
    } else {
      userInfo.versionUpgrade = null;
    }
  } catch(e) {
    // TODO: 
    console.warn(e);
  }

  window._USERINFO = userInfo;
  import('./configs/run').then(run => {
    let runner;
    if (run.default) {
      runner = run.default;
    } else {
      runner = run;
    }
    runner().then(run => {
      run.start();
    })
  });
};

startApp();