/**
 * wrapper around getComputedStyle, to fix issues with Firefox returning null when
 * called inside of a hidden iframe
 *
 * @access private
 * @function computedStyle
 * @param {HTMLElement|SVGElement} - The element we want to find the computed styles of
 * @param {string|null} [pseudoSelector]- An optional pseudo element selector (e.g. :before), of null if none
 * @returns {CSSStyleDeclaration}
 */

function computedStyle(elem, pseudo, prop) {
  var result;

  if ('getComputedStyle' in window) {
    result = getComputedStyle.call(window, elem, pseudo);
    var console = window.console;

    if (result !== null) {
      if (prop) {
        result = result.getPropertyValue(prop);
      }
    } else {
      if (console) {
        var method = console.error ? 'error' : 'log';
        console[method].call(console, 'getComputedStyle returning null, its possible modernizr test results are inaccurate');
      }
    }
  } else {
    result = !pseudo && elem.currentStyle && elem.currentStyle[prop];
  }

  return result;
}

const test_cssvhunit = () => {
  const testNode = document.createElement('div');
  testNode.style.cssText = 'height: 50vh';
  document.body.appendChild(testNode);
  const height = parseInt(window.innerHeight / 2, 10);
  const testNodeHeight = parseInt(computedStyle(testNode, null, 'height'), 10);
  document.body.removeChild(testNode);
  return testNodeHeight === height;
};

const test_preload = () => {
  const testPreloadLink = document.createElement('link');
  testPreloadLink.rel = 'preload';
  let supportPreload = false;
  try {
    supportPreload = testPreloadLink.relList.supports('preload');
  } catch(e) {
    console.warn('The browser does not supports link[rel="preload"');
  }
  return supportPreload;
};

export default {
  cssvhunit: test_cssvhunit(),
  preload: test_preload(),
};